        <br><br><br>
        <template v-if="filter_data && show_page == 'package'">
            <nav class="navbar fixed-bottom navbar-light bg-light border" v-if="filter_data.length > pageSize">
                <div class="container-fluid d-flex justify-content-center">
                    <pagination class="d-block d-sm-none" v-model="page" :records="filter_data.length" v-if="pack_data" :per-page="pageSize" :options="opt1" />
                    <pagination class="d-none d-sm-block d-lg-none" v-model="page" :records="filter_data.length" v-if="pack_data" :per-page="pageSize" :options="opt2" />
                    <pagination class="d-none d-lg-block" v-model="page" :records="filter_data.length" v-if="pack_data" :per-page="pageSize" :options="opt3" />
                </div>
            </nav>
        </template>
    </div>
</template>

<script>
import PaginationTemplate from '@/components/PaginationTemplate.vue'
import Offcanvas from 'bootstrap/js/dist/offcanvas.js'
import Modal from 'bootstrap/js/dist/modal.js'
import Dropdown from 'bootstrap/js/dist/dropdown.js'
export default {
    name: "Main",
    data(){
        return{
            error: null,
            card_modal: null,
            display_card: null,
            display_page: "active",
            show_page: "Ancient_seal",
            draw_data:{
                black: null,
                white: null,
            },
            pack_data: null,
            filter_data: null,
            filter_: {
                attr: new Array(),
                race: new Array(),
                star: new Array(),
                level: new Array()
            },
            sort:"attr",
            page: 1,
            pageSize: 120,
            opt1:{
                chunk:3,
                template: PaginationTemplate,
            },
            opt2:{
                chunk:5,
                template: PaginationTemplate,
            },
            opt3:{
                chunk:10,
                template: PaginationTemplate,
            },
        }
    },
    created(){
        this.$http.post('https://website-api.tosgame.com/api/checkup/login?token=&uid=' + this.main_data.uid + '&auth=' + this.main_data.auth).then((token) => {
            this.$http.get('https://website-api.tosgame.com/api/checkup/getUserProfile?token=' + token.data.token + '&targetUid=' + this.$route.params.uid).then((res) => {
                document.title = res.data.userData.displayName + this.$t('message.name_title')
                for(let card in res.data.userData.cards){
                    let monster = this.monster_data.find((i) => {
                        return i.id === res.data.userData.cards[card].id
                    })
                    for (var attrname in monster) { res.data.userData.cards[card][attrname] = monster[attrname]; }
                }
                res.data.userData.cards.sort(function(a, b) {
                    if(b.attribute == a.attribute){
                        return a.id - b.id;
                    }
                    return a.attribute - b.attribute
                });
                this.pack_data = res.data.userData.cards
                this.filter_data = res.data.userData.cards.filter(() => {
                    return true;
                })
                res.data.userData.cards.sort((a, b) => {
                    if(a.id == b.id){
                        if(a.level == b.level){
                            if(a.skillLevel == b.skillLevel){
                                return b.enhanceLevel - a.enhanceLevel
                            }
                            return b.skillLevel - a.skillLevel
                        }
                        return b.level - a.level
                    }
                    return b.id - a.id
                })
                for(let type in this.seal_data){
                    this.draw_data[type] = {}
                    for(let sec in this.seal_data[type]){
                        this.draw_data[type][sec] = []
                        for(let cards in this.seal_data[type][sec]){
                            this.seal_data[type][sec][cards].sort((a, b) => {
                                return b - a
                            })
                            let monster = null
                            for(let i in this.seal_data[type][sec][cards]){
                                monster = res.data.userData.cards.find((j) => {
                                    return j.id == this.seal_data[type][sec][cards][i]
                                })
                                if(monster){
                                    break
                                }
                            }
                            if(monster){
                                let monster_data = this.monster_data.find((i) => {
                                    return i.id === monster.id
                                })
                                for (let attrname in monster_data) { monster[attrname] = monster_data[attrname];}
                                monster.get = true
                                this.draw_data[type][sec].push(monster)
                            }else{
                                let monster = this.monster_data.find((i) => {
                                    return i.id === this.seal_data[type][sec][cards][0]
                                })
                                monster.get = false
                                this.draw_data[type][sec].push(monster)
                            }
                        }
                    }
                }
            }).catch((err) => {
                this.error = err.response
                this.filter_data = {}
            })
        }).catch((err) => {
            this.error = err.response
            this.filter_data = {}
        })
    },
    mounted(){
        [].slice.call(document.querySelectorAll('.offcanvas')).map(function (offcanvasEl) {
            return new Offcanvas(offcanvasEl)
        })
        new Array().slice.call(document.querySelectorAll('.dropdown-toggle')).map(function (dropdownToggleEl) {
            return new Dropdown(dropdownToggleEl)
        })
        this.card_modal = new Modal(this.$refs.card_modal)
    },
    computed:{
        filter_count(){
            return this.filter_.attr.length + this.filter_.race.length + this.filter_.star.length + this.filter_.level.length
        },
        indexStart() {
            return (this.page - 1) * this.pageSize;
        },
        indexEnd() {
            return this.indexStart + this.pageSize;
        },
        paginated() {
            return this.filter_data.slice(this.indexStart, this.indexEnd);
        },
        monster_data(){
            return this.$store.state.monster_data
        },
        monster_active(){
            return this.$store.state.monster_active
        },
        monster_leader(){
            return this.$store.state.monster_leader
        },
        monster_team(){
            return this.$store.state.monster_team
        },
        seal_data(){
            return this.$store.state.seal_data
        },
        main_data(){
            return this.$store.state.main_data
        }
    },
    methods:{
        avatar_src(id){
            if(parseInt(id) < 10){
                id = "000" + id
            }else if(parseInt(id) < 100){
                id = "00" + id
            }else if(parseInt(id) < 1000){
                id = "0" + id
            }
            return "https://web-assets.tosconfig.com/gallery/icons/" + id + ".jpg"
        },
        lv_status(card){
            let str = ""
            if(card.maxLevel == card.level){
                if(card.maxSkill == card.skillLevel){
                    if(card.maxRefine != 0 &&  card.maxRefine == card.enhanceLevel){
                        str += "<span class='fw-bold fst-italic text-nowrap text-warning'>AllMax</span>"
                    }else{
                        str += "<span class='fw-bold fst-italic text-nowrap text-warning'>DualMax</span>"
                    }
                }else{
                    str += "<span>LV." + this.$t("message.max") + "</span>"
                }
            }else{
                str += "<span>LV." + card.level + "</span>"
            }
            return str
        },
        filter(){
            this.filter_data = this.pack_data.filter((item) => {
                let tmp1 = 0, tmp2 = 0, tmp3 = 0, tmp4 = 0
                if(this.filter_.attr.length == 0){
                    tmp1 = 1
                }else if(this.filter_.attr.indexOf(item.attribute.toString()) != -1){
                    tmp1 = 1
                }
                if(this.filter_.race.length == 0){
                    tmp2 = 1
                }else if(this.filter_.race.indexOf(item.race.toString()) != -1){
                    tmp2 = 1
                }
                if(this.filter_.star.length == 0){
                    tmp3 = 1
                }else{
                    if(this.filter_.star.indexOf(item.star.toString()) != -1){
                        tmp3 = 1
                    }else if(this.filter_.star.indexOf("5d") != -1 && item.star < 5){
                        tmp3 = 1
                    }
                }
                if(this.filter_.level.length == 0){
                    tmp4 = 1
                }else{
                    if(item.maxLevel == item.level){
                        if(item.maxSkill == item.skillLevel){
                            if(item.maxRefine != 0 &&  item.maxRefine == item.enhanceLevel){
                                if(this.filter_.level.indexOf("AM") != -1){
                                    tmp4 = 1
                                }
                            }else{
                                if(this.filter_.level.indexOf("DM") != -1){
                                    tmp4 = 1
                                }
                            }
                        }else{
                            if(this.filter_.level.indexOf("LM") != -1){
                                tmp4 = 1
                            }
                        }
                    }
                }
                return tmp1 && tmp2 && tmp3 && tmp4
            })
        },
        display_leader(id){
            let leader = this.monster_leader.find((item) => {
                return item[0] == id
            })
            leader = leader[1]
            leader.name = leader.name.replace(/['"]+/g, '')
            leader.description = leader.description.replace(/['"]+/g, '')
            return leader
        },
        display_active(id){
            let active = this.monster_active.find((item) => {
                return item[0] == id
            })
            active = active[1]
            active.name = active.name.replace(/['"]+/g, '')
            active.description = active.description.replace(/['"]+/g, '')
            return active
        },
        display_team(id){
            let team = this.monster_team.find((item) => {
                return item[0] == id
            })
            return team[1].replace(/['"]+/g, '')
        },
        sort_attr(){
            this.filter_data.sort(function(a, b) {
                if(b.attribute == a.attribute){
                    return a.id - b.id;
                }
                return a.attribute - b.attribute
            });
        },
        sort_time(){
            this.filter_data.sort(function(a, b) {
                return b.acquiredAt - a.acquiredAt
            });
        },
        sort_id(){
            this.filter_data.sort(function(a, b) {
                return a.id-b.id
            });
        },
    },
    watch:{
        'filter_.attr'(){
            this.page = 1
            this.filter();
        },
        'filter_.race'(){
            this.page = 1
            this.filter();
        },
        'filter_.star'(){
            this.page = 1
            this.filter();
        },
        'filter_.level'(){
            this.page = 1
            this.filter();
        },
        sort(){
            if(this.sort == "attr"){
                this.sort_attr()
            }
            if(this.sort == "time"){
                this.sort_time()
            }
            if(this.sort == "id"){
                this.sort_id()
            }
        }
    }
}
</script>

<style scoped>
    /* HIDE RADIO */
    [type=checkbox] { 
        position: absolute;
        opacity: 0;
        width: 0;
        height: 0;
    }

    /* IMAGE STYLES */
    [type=checkbox] + img {
        cursor: pointer;
        filter: brightness(.4);
    }

    /* CHECKED STYLES */
    [type=checkbox]:checked + img {
        filter: brightness(1);
    }
</style>